<template>
  <div class="container user">
    <div>
      <img style="width: 100vw;margin-top: -2rem" src="../assets/images/user/user_bg.png" alt="">
      <div class="user-content">
        <img class="user-head" src="../assets/images/user/user_head.jpg" alt="">
        <span class="user-name">{{userInfo.username}}</span>
      </div>
      <div class="user-info">
        <div class="info-unit">
          <span>666 小时</span>
          <span class="info-title">累计工作时长</span>
        </div>
        <div class="info-unit">
          <span>250 天</span>
          <span class="info-title">累计工作</span>
        </div>
        <div class="info-unit">
          <span>12 人</span>
          <span class="info-title">我的关注</span>
        </div>
      </div>
    </div>
<!--    <van-cell-group>-->
<!--      <van-cell title="姓名" center :value="userInfo.username"/>-->
<!--    </van-cell-group>-->
<!--    <van-cell-group>-->
<!--      <van-cell title="手机号码" center :value="userInfo.telephone"/>-->
<!--    </van-cell-group>-->
<!--    <van-cell-group>-->
<!--      <van-cell is-link to="/resetPassword" center title="重置密码" value=""/>-->
<!--    </van-cell-group>-->
    <van-button style="margin-top: 2rem" type="primary" size="large" @click="exitLogin">退出登录</van-button>
  </div>
</template>

<script>
import session from '../api/index';
export default {
    name: 'MyInfo',
    inject: ['reload'],
    components: {},
    data() {
        return {
            isShowPrompted: false,
            radio: '1',
            userInfo: {username: '', telephone: ''}
        };
    },
    mounted() {
        this.$nextTick(function () {
            this.$store.dispatch('saveUserInfo'); // 使用.dispatch改变store中state里的数据
            this.userInfo.username = this.$store.state.userInfo.username;
        });
    },
    methods: {
        goBack: function () {
            this.$router.go(-1);
        },
        exitLogin: function () {
            session.clearCookie('token');
            this.reload();
            this.$router.push({
                path: '/'
            });
        },
        addWork() {
            this.isShowPrompted = true;
        },
        ackAdd() {
            console.log(this.radio);
            if (this.radio === '1') {
                this.$store.commit('changeJudgeAdd', true);
                this.$router.push({
                    name: 'EditWorkDaily',
                    query: {workDailyId: -1}
                });
            } else if (this.radio === '2') {
                this.$store.commit('changeJudgeAdd', true);
                this.$router.push({
                    name: 'EditWorkItem',
                    query: {introId: -1}
                });
            }
        },
        cancleAdd() {
            this.isShowPrompted = false;
            this.active = 2;
        }
    }
};
</script>

<style lang="less">
  .container {
    .van-button--large {
      display: block;
      width: 90%;
      margin: auto;
      border-radius: 25px;
      background-color: #fe8f01;
      border: 1px solid #fe8f01;
      margin-top: 8%;
      height: 40px;
      line-height: 40px;
    }
    .return-btn{
      color: #828282;
      background-color: #EEF0F3;
      border: 0.04878rem solid #bfbfbf;
    }
    .van-popup--center {
      width: 80%;
      border-radius: 10px;

      .prompted-box {
        padding: 20px;
        text-align: center;
        .van-radio-group{
          margin-top: 10%;
          .van-radio {
            margin-top: 10%;
          }
        }
        span{
          display: inline-block;
        }

        .van-button {
          border-radius: 0;
          width: 30%;
          height: 30px;
          line-height: 0;
          display: inline;
          margin-left: 10%;
          margin-top: 10%;
          border-radius: 5px;
        }
      }
    }
  }
  .user{
    .user-content {
      display: flex;
      position: absolute;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      width: 100vw;
      top: 1rem;
      .user-head{
        width: 4rem;
        height: 4rem;
        border-radius: 50%;
      }
      .user-name{
        color: white;
        font-size: 0.7rem;
        margin-top: 0.3rem;
      }
    }
  }
  .user-info {
    display: flex;
    background-color: white;
    width: 90vw;
    margin-left: 5vw;
    margin-top: -2rem;
    padding: 10px;
    z-index: 9999;
    box-sizing: border-box;
    position: absolute;
    border-radius: 0.3rem;
    box-shadow: 0 0 6px 2px #fc8e3e80;
    .info-unit {
      display: flex;
      flex-direction: column;
      width: 33.33%;
      text-align: center;
      color: #fe8f01;
      font-size: 0.8rem;
      font-weight: bold;
      .info-title{
        color: gray;
        font-size: 0.55rem;
        font-weight: normal;
      }
    }
  }
</style>
